@charset "UTF-8";
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), "../fonts/opensans.woff";
  font-display: swap;
}
@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), "../fonts/opensansbold.woff";
  font-display: swap;
}
@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/opensanslight.woff2") format("woff2"), "../fonts/opensanslight.woff";
  font-display: swap;
}
html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  background-color: #ffffff;
}

.wrapper {
  margin: 0 auto;
  width: 280px;
}
@media (min-width: 660px) {
  .wrapper {
    width: 620px;
  }
}
@media (min-width: 1200px) {
  .wrapper {
    width: 940px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: space-between;
  height: 66px;
  z-index: 1;
}
@media (min-width: 660px) {
