}

.header__logo {
  padding: 22px 20px 21px;
}
@media (min-width: 660px) {
  .header__logo {
    padding: 27px 20px 43px;
  }
}

.header__logo:hover .header__logotype {
  fill: #d22856;
}

.header__logo:active .header__logotype {
  fill: #ffffff;
  opacity: 0.3;
}

.header__logotype {
  width: 74px;
  height: 23px;
  fill: #ffffff;
}
.header__logotype--tab {
  display: none;
}
.header__logotype--desk {
  display: none;
}
.header__logotype:hover {
  fill: #d22856;
}
@media (min-width: 660px) {
  .header__logotype {
    width: 145px;
    height: 40px;
  }
  .header__logotype--mob {
    display: none;
