.specifications {
  padding-top: 55px;
}

.specifications__wrapper {
  @media (min-width: $desktop-width) {
    display: flex;
    width: 892px;
  }
}

.specifications__list {
  margin: 0;
  padding: 0;
  padding-top: 105px;
  list-style-type: none;
  background-color: $base-bg;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding-bottom: 50px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 366px;
  }
}

.specifications__title {
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  margin: 0;
  margin-bottom: 53px;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: 54px;
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
    line-height: 68px;
  }
}

.specifications__item-title {
  color: #000000;
  font-size: 18px;
  line-height: 14px;
  font-weight: bold;
  margin: 0;
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    font-size: 30px;
    line-height: 30px;
  }
}

.specifications__text {
  line-height: 20px;
  margin: 0;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}

.specifications__item {
  position: relative;

  &:not(:last-child) {
    margin-bottom: 43px;
  }

  @media (min-width: $tablet-width) {
    width: 310px;
    margin-left: auto;

    &:not(:last-child) {
      margin-bottom: 46px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 252px;
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 0;
      margin-right: 68px;
    }
  }
}

.specifications__item:nth-child(1) {
  @media (min-width: $desktop-width) {
    &::before {
      content: "";
      display: block;
      position: absolute;
      background-image: url(../img/screen-1d.png);
      bottom: 250px;
      left: -20px;
      width: 300px;
      height: 301px;

      @media (min-resolution: 2dppx) {
        background-image: url(../img/screen-1d@2x.png);
        background-size: 300px auto;
      }
    }
  }
}

.specifications__item:nth-child(2) {
  @media (min-width: $desktop-width) {
    &::before {
      content: "";
      display: block;
      position: absolute;
      background-image: url(../img/screen-2d.png);
      bottom: 250px;
      left: -20px;
      width: 300px;
      height: 301px;

      @media (min-resolution: 2dppx) {
        background-image: url(../img/screen-2d@2x.png);
        background-size: 300px auto;
      }
    }
  }
}

.specifications__item:nth-child(3) {
  @media (max-width: $mobile-width-only) {
    padding-bottom: 325px;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 62px;
    background-image: url(../img/screen3-m.png);
    background-repeat: no-repeat;
    width: 157px;
    height: 277px;

    @media (min-resolution: 2dppx) {
      background-image: url(../img/screen3-m@2x.png);
      background-size: 157px auto;
    }

    @media (min-width: $tablet-width) {
      background-image: url(../img/screen3-t.png);
      left: -278px;
      bottom: -50px;
      height: 482px;
      width: 268px;

      @media (min-resolution: 2dppx) {
        background-image: url(../img/screen3-t@2x.png);
        background-size: 268px auto;
      }
    }

    @media (min-width: $desktop-width) {
      background-image: url(../img/screen-3d.png);
      bottom: 250px;
      left: -20px;
      width: 300px;
      height: 301px;

      @media (min-resolution: 2dppx) {
        background-image: url(../img/screen-3d@2x.png);
        background-size: 300px auto;
      }
    }
  }
}
